
nav {
  position: fixed;
  top: 0;
  right: 0;

  .vh {
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px);
    padding:0 !important;
    border:0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
  }

  [data-menu-component] {
    position: relative;
    display: inline-block;
  }

  label {
    display: inline-block;
  }

  [role="menu"] ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  label[data-opens-menu] {
    color: var(--black-coffee) !important;
    text-decoration: none !important;
    margin: 1em;
    background-color: white;
    border-bottom: 2px transparent var(--rosso-corsa);
    &:hover, &:focus {
      //margin-bottom: -2px;
      color: var(--black-coffee);
      text-decoration: none;
      border-bottom: 2px solid var(--rosso-corsa);
    }
  }

  li a {
    color: var(--black-coffee);
    text-decoration: none;
    background-color: white;
    &:hover, &:focus, &:focus-within, &:focus-visible {
      margin-bottom: -2px;
      color: var(--black-coffee);
      text-decoration: none;
      border-bottom: 2px solid var(--rosso-corsa);
    }
    //a {
    //  color: var(--black-coffee);
    //  text-decoration: none;
    //}
  }


  [data-opens-menu], [role="menu"] li {
    line-height: 1;
    text-align: left;
    border: 0;
    color: darkgrey;
    padding: 0.33rem 0.5rem;
    text-decoration: none;
    //&:hover, &:focus, &:focus-within, &:focus-visible {
    //  //margin-bottom: -2px;
    //  border-bottom: 2px solid var(--rosso-corsa);
    //}
    //a {
    //  color: var(--black-coffee);
    //  text-decoration: none;
    //}
  }
  [type="checkbox"]:focus + [data-opens-menu] {
    outline: none;
    //background: white;
    //border-bottom: 2px solid var(--rosso-corsa);
    //margin-bottom: -2px;
  }

  [role="menu"] a:focus {
    outline: none;
    //background: white;
    //border-bottom: 2px solid var(--rosso-corsa);
    //margin-bottom: -2px;
  }

  :checked + label .expanded-text {
    display: inline;
  }

  :checked + label .collapsed-text {
    display: none;
  }

  [type="checkbox"]:not(:checked) + label .expanded-text {
    display: none;
  }

  [type="checkbox"]:not(:checked) + label .collapsed-text {
    display: inline;
  }

  [role="menu"] {
    position: absolute;
    background-color: white;

    right: 0;
    display: none;
    //text-align: right;
  }

  :checked ~ [role="menu"] {
    display: block;
  }

  [data-menu-origin="right"] {
    left: auto;
    right: 0;
  }

  [role="menu"] a {
    display: block;
    min-width: 100%;
    margin-top: 0.125rem;
    white-space: nowrap;
    background-color: white;

  }
}